import React from 'react';
import ReactDOM from 'react-dom';
import "./index.css";
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';
import Hidden from '@material-ui/core/Hidden';

import TabsComponent from './Components/TabsComponent'
import CategoriesComponent from './Components/CategoriesComponent'
import MainMenuComponent from './Components/MainMenuComponent'
import HeaderComponent from './Components/HeaderComponent'
import LoginComponent from './Components/LoginComponent'
import SignUpComponent from './Components/SignUpComponent'
import AddSurveyComponent from './Components/AddSurveyComponent'
import ConfirmRegistration from './Components/ConfirmRegistration'
import { BrowserRouter as Router, Route } from 'react-router-dom'
import InfiniteScroll from './Components/InfiniteScroll'
import StatisticsComponent from './Components/StatisticsComponent'
import ProfileComponent from "./Components/ProfileComponent";
import { properties } from './Properties/Properties.js';
import { createBrowserHistory } from "history";
import { Link } from '@material-ui/core';
import Typography from '@material-ui/core/Typography';
import RandomPageComponent from "./Components/Pages/RandomPageComponent";
import SurveyPageComponent from "./Components/Pages/SurveyPageComponent";
import NewsPageComponent from "./Components/Pages/NewsPageComponent";
import { createMuiTheme } from '@material-ui/core/styles';
import { grey } from '@material-ui/core/colors';
import AboutPageComponent from "./Components/Pages/AboutPageComponent";
import DonatePageComponent from "./Components/Pages/DonatePageComponent";


import { ThemeProvider } from '@material-ui/core/styles';
import PopupComponent from "./Components/PopupComponent"
import Fade from '@material-ui/core/Fade';

function Copyright() {
  return (
    <Typography variant="body2" color="textSecondary" align="center">
      {'Copyright  '}
      <Link color="inherit" href="/home">
        runningwilder.github.io/SurveyIt
      </Link>{' '}
      {new Date().getFullYear()}
      {'.'}
    </Typography>
  );
}

export const UserContext = React.createContext({})
export const PopupContext = React.createContext("help me")
function App() {
  const [message, setMessage] = React.useState("")
  const [hidden, setHidden] = React.useState(true)
  const [state, setState] = React.useState({
    loggedIn: false,
    username: "",
    userId: 0,
    reload: () => { },
    activeType: "",
    activeTag: ""
  })

  // This function executes when categoriesComponent is triggered. It fetches new data
  // depending on categories and sets Type of surveys filter to default (empty string "")
  // thus updating child TabsComponent and erasing filters
  const customFunc = (tag, type) => {
    // console.log('reloading...' + "tag = " + tag + " type = " + type)
    setState({ ...state, activeType: type })
    setTimeout(() => { state.reload && state.reload(tag, type) }, 1000)
  }

  const handler = load => {
    // console.log('setting reload to ' + load)
    setState({ ...state, reload: load })
  }
  const [userInfo, setUserInfo] = React.useState({
    username: null,
    userId: null,
    loggedIn: false
  })
  const checkLoginStatus = () => {
    console.log("checking auth")
    fetch(`${properties.address}/logged_in`,
      {
        method: 'GET',
        credentials: 'include',
      })
      .then(response => {
        if (response.status === 200 && !state.loggedIn) {
          return response.json()
        }
        else {
          setUserInfo({
            ...userInfo,
            username: null,
            userId: null,
            loggedIn: false
          })
        }
      })
      .then(result => {
        if (result) {
          console.log(result)
          setUserInfo({
            ...userInfo,
            username: result.username,
            userId: result.user_id,
            loggedIn: true
          })
        }

      })
  }
  React.useEffect(() => {
    checkLoginStatus()
  }, [])

  const history = createBrowserHistory()
  let value = history.location.pathname === "/new" || history.location.pathname === "/home" ? 1 : 0

  const setMessageFunc = msg => {
    setMessage(msg)
    setHidden(false)
    setTimeout(() => {
      setHidden(true)
    }, 3000)
  }
  return (
    <UserContext.Provider value={userInfo}>
      <PopupContext.Provider value={setMessageFunc}>
        <Router>
          <Box>
            <Fade in={!hidden} timeout={500}>
              <div>
                <PopupComponent message={message} />
              </div>
            </Fade>


            <Route exact={true} path={[`${properties.repo}/`, `${properties.repo}/new`, `${properties.repo}/home`, `${properties.repo}/top`]}>
              <Box style={{ paddingLeft: "20px", paddingRight: "20px", paddingTop: "20px" }}>
                <Grid container spacing={3}>
                  <Grid item xs={12} sm={9} >
                    <TabsComponent DefaultType={state.activeType} Load={customFunc} Value={value}>
                      <InfiniteScroll ActiveType={state.activeType} UserName={state.username} UserId={state.userId} SetReloadFunction={handler} Mode="Top" Surveys={[]} Error={false} HasMore={true} IsLoading={false} Index={0} />
                      <InfiniteScroll ActiveType={state.activeType} UserName={state.username} UserId={state.userId} SetReloadFunction={handler} Mode="New" Surveys={[]} Error={false} HasMore={true} IsLoading={false} Index={0} />
                    </TabsComponent>
                  </Grid>
                  <Grid item sm={3} style={{ paddingRight: "10px" }}>
                    <Hidden only="xs">
                      <Box style={{ position: "sticky", top: "0" }}>
                        <CategoriesComponent LoggedIn={state.loggedIn} Load={customFunc}></CategoriesComponent>
                      </Box>
                      <MainMenuComponent />
                    </Hidden>
                  </Grid>
                </Grid>
              </Box>
            </Route>
            <Route path={`${properties.repo}/random`}><RandomPageComponent /></Route>
            <Route path={`${properties.repo}/login`}><LoginComponent handleLoggedIn={checkLoginStatus} /></Route>
            <Route path={`${properties.repo}/createSurvey`}><AddSurveyComponent /></Route>
            <Route path={`${properties.repo}/profile`}><ProfileComponent /></Route>
            <Route path={`${properties.repo}/register`}><SignUpComponent /></Route>
            <Route path={`${properties.repo}/registrationConfirm`}><ConfirmRegistration /></Route>
            <Route path={`${properties.repo}/survey/:id`}><SurveyPageComponent /></Route>
            <Route path={`${properties.repo}/news`}><NewsPageComponent /></Route>
            <Route path={`${properties.repo}/donate`}> <DonatePageComponent /> {/* <SendFeedbackComponent /> */} </Route>
            <Route path={`${properties.repo}/about`}><AboutPageComponent /></Route>
            <Copyright />
          </Box>
        </Router>
      </PopupContext.Provider>
    </UserContext.Provider>
  );
}

const christmasTheme = createMuiTheme({

  overrides: {
    MuiListItem: {
      root: {
        "&$selected": {
          // backgroundColor: grey[800],
          background: "linear-gradient(21deg, rgba(54,54,61,0.8132440476190477) 0%, rgba(95,154,164,0.7740283613445378) 100%)"
        },
        "&$selected:hover": {
          backgroundColor: grey[700],
        },
      },
    }
  },
  shape: {
    borderRadius: 0,
  },
  palette: {
    primary: {
      main: "#ccced8"
    },
    secondary: {
      main: "#3d608c"
    },
    info: {
      main: "#54c0ef8c"
    }
    // type: "dark"
  },
  typography: {
    fontSize: 13,
    // fontFamily: "Palatino Linotype, Book Antiqua, serif",
    // fontFamily: "Lucida Sans Unicode, Lucida Grande, serif"
    // fontFamily: "Lucida Console, Monaco, monospace"
    fontFamily: "Comic Sans MS, cursive, sans-serif"
  }
});

ReactDOM.render(<ThemeProvider theme={christmasTheme}><App /></ThemeProvider>,
  document.getElementById("root"));