import React from 'react';

import Radio from '@material-ui/core/Radio';
import FormControl from '@material-ui/core/FormControl';
import FormLabel from '@material-ui/core/FormLabel';
import FormHelperText from '@material-ui/core/FormHelperText';
import Button from '@material-ui/core/Button';
import FormGroup from '@material-ui/core/FormGroup';

import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';

export default function RatingSurveyComponent(props) {
  props = props.props
  const [state, setState] = React.useState({
    error: false,
    voted: props.surveyJson.answered,
    helperText: props.HelperText,
    errors: {
      option: "",
      duplicate: ""
    },
    selectedValues: new Array(props.surveyJson.rows.length),
  })

  const validate = () => {
    let duplicateError = ''
    let optionError = ''
    let totalError = false
    let helperText = ''

    if ((new Set(state.selectedValues.filter(val => val !== undefined))).size !== state.selectedValues.filter(val => val !== undefined).length) {
      duplicateError = '       / '
      totalError = true
      helperText = duplicateError
    }

    if (state.selectedValues.filter(val => val !== undefined).length < props.surveyJson.rows.length) {
      optionError = '  '
      totalError = true
      helperText = helperText + '\n' + optionError
    }

    if (totalError) {
      setState({ ...state, error: true, helperText: helperText })
      return false
    }
    setState({ ...state, error: false, helperText: props.HelperText })
    return true
  }

  const handleSubmit = e => {
    e.preventDefault()
    let submitOptions = {
      "surveyId": props.surveyJson.id,
      "rowsColumns": state.selectedValues
    }
    props.handleSubmit(e, submitOptions, validate, processDataFunc)
  }

  const handleChange = (event, rowIndex) => {
    let oldValues = state.selectedValues
    if (!oldValues.includes(event.target.value)) {
      oldValues[rowIndex] = event.target.value
      setState({ ...state, selectedValues: oldValues });
    }
    else {
      let i = oldValues.indexOf(event.target.value)
      oldValues[i] = undefined
      oldValues[rowIndex] = event.target.value
      setState({ ...state, selectedValues: oldValues })
    }
  }

  const processDataFunc = (e, result) => {
    console.log('processing from Rating')
  }

  return <FormControl
    component="fieldset"
    error={state.error}
  >
    {!props.flipped && <form onSubmit={handleSubmit}>
      <FormLabel component="legend">{props.surveyJson.question}</FormLabel>
      <FormGroup>
        {!props.Answered && <TableContainer component={Paper}>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell></TableCell>
                {props.surveyJson.columns.map((column, columnIndex) => (
                  <TableCell key={columnIndex} align="center">{column}</TableCell>
                ))}
              </TableRow>
            </TableHead>
            <TableBody>
              {props.surveyJson.rows.map((row, rowIndex) => (
                <TableRow key={rowIndex}>
                  <TableCell component="th" scope="row">
                    {row}
                  </TableCell>
                  {props.surveyJson.columns.map((column, columnIndex) => (

                    <TableCell key={columnIndex} align="center" >
                      <Radio
                        name={"r" + rowIndex}
                        onChange={e => { handleChange(e, columnIndex) }}
                        checked={state.selectedValues[columnIndex] === props.surveyJson.rows[rowIndex]}
                        value={props.surveyJson.rows[rowIndex]}
                      />
                    </TableCell>
                  ))}
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>}
      </FormGroup>
      <FormHelperText>{state.helperText}</FormHelperText>
      <Button type="submit" variant="contained" color="primary"></Button>
    </form>}
    {/* {props.Answered &&  <StatisticsComponent Rows={props.Rows} Columns={props.Columns} Type="Rating" SurveyId={SurveyId}></StatisticsComponent>} */}

  </FormControl>
}