import React from 'react';
import TabPanelComponent from './TabPanelComponent'

import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Box from '@material-ui/core/Box';
import Grid from '@material-ui/core/Grid';
import Tooltip from '@material-ui/core/Tooltip'; 
import Paper from '@material-ui/core/Paper';
import IconButton from '@material-ui/core/IconButton';
import { makeStyles } from '@material-ui/core/styles';
import { Link } from 'react-router-dom'

import FormatListBulletedIcon from '@material-ui/icons/FormatListBulleted';
import ViewListIcon from '@material-ui/icons/ViewList';
import AppsIcon from '@material-ui/icons/Apps';
import TestComponent from './TestComponent'

const useStyles = makeStyles((theme) => ({
    tabs: {
      backgroundColor: theme.palette.secondary.main,
    },
    mainBox: {
      backgroundColor: "transparent",
    },
    indicator: {
      backgroundColor: 'white',
    }
}))

export default function TabsComponent(props){
    const [value, setValue] = React.useState(props.Value)
    const {tabs} = props
    const classes = useStyles()
    const [gridBody, setGrid] = React.useState(<div>Loading...</div>)
    const handleChange = (event, newValue) => {
      setValue(newValue);
      setGrid(<div>...Loading</div>)
    };
    const [activeType, setActiveType] = React.useState("")
    
    React.useEffect(() => {
      setGrid(props.children[value])
      // setGrid(React.cloneElement(props.children[value], {ActiveType: activeType}))
    }, [value])
    
    React.useEffect(() => {
      setActiveType(props.DefaultType)
    },[props.DefaultType])

    const handleFilter = type => {
      let newType = activeType === type ? "" : type
      setActiveType(newType)
      props.Load(undefined, newType)
    }
    React.useEffect(() => {
      setGrid(props.children[value])
    }, [activeType])

    return <Grid container spacing={3}  alignItems="flex-start" justify = "flex-start">
              <Grid  item xs={12}>
                <Paper className = {classes.tabs}>
                  <Tabs TabIndicatorProps={{style: { backgroundColor: 'white'}}}  selectionFollowsFocus centered value={value} aria-label="simple tabs example">
                      <Tab onClick = {e=> handleChange(e, 0)} component = {Link} to = "top" label="TOP" id = "simple-tab-0" aria-controls = "simple-tabpanel-0" />
                      <Tab onClick = {e=> handleChange(e, 1)} component = {Link} to = "new" label="NEW" id = "simple-tab-1" aria-controls = "simple-tabpanel-1"  />

                      <Tooltip title = " ">
                        <IconButton 
                          color = {activeType === "SINGLE" ? "secondary" : "primary"}
                          onClick = {e => {handleFilter("SINGLE")}}
                        >
                          <ViewListIcon />
                        </IconButton>
                      </Tooltip>
                      <Tooltip title = " ">
                        <IconButton 
                          color = {activeType === "MULTIPLE" ? "secondary" : "primary"}
                          onClick = {e => {handleFilter("MULTIPLE")}}
                        >
                          <FormatListBulletedIcon />
                        </IconButton>
                      </Tooltip>
                      <Tooltip title = " ">
                        <IconButton 
                          color = {activeType === "RATING" ? "secondary" : "primary"}
                          onClick = {e => {handleFilter("RATING")}}
                        >
                          <AppsIcon />
                        </IconButton>
                      </Tooltip>
                  </Tabs>
                </Paper>
              </Grid>
              <Grid  item xs={12} id = "surveys">
                <Paper  className = {classes.mainBox}>
                  <Box  >
                    {gridBody}
                  </Box>
                </Paper>
              </Grid>
            </Grid>
}