import React from 'react';

import Card from '@material-ui/core/Card';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import { properties } from '../Properties/Properties.js'
import DraftsIcon from '@material-ui/icons/Drafts';
import { useHistory } from "react-router-dom";
import { Link } from 'react-router-dom';
import { NavLink } from 'react-router-dom';
import ContactSupportIcon from '@material-ui/icons/ContactSupport';
import HomeIcon from '@material-ui/icons/Home';
import FreeBreakfastIcon from '@material-ui/icons/FreeBreakfast';
import UpdateIcon from '@material-ui/icons/Update';
import MuiListItem from "@material-ui/core/ListItem";
import { makeStyles, withStyles } from "@material-ui/core/styles";
import AcUnitIcon from '@material-ui/icons/AcUnit';
import { UserContext } from '../index'


const useStyles = makeStyles((theme) => ({

    list: {
        padding: 0
    },
    checkbox: {
        padding: 3
    },
    inactive: {
        color: theme.palette.grey[300],
        textDecoration: "none"
    },
    menu: {
        backgroundColor: "#4c546391"
    },
    icon: {
        color: theme.palette.grey[300],
    },
    root: {
        "&:hover": {
            // backgroundColor: theme.palette.grey[700],
            background: "linear-gradient(21deg, rgba(54,54,61,0.5723476890756303) 0%, rgba(159,181,185,0.5639443277310925) 100%)"
            // backgroundColor: "linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(9,9,121,1) 35%, rgba(0,212,255,1) 100%)"
        }
    },
    rotate: {
        // transform: "rotate(70deg)"
        animation: `spin 15s linear infinite`
    }

}))
export default function MainMenuComponent(props) {
    const userInfo = React.useContext(UserContext)
    const classes = useStyles()
    let history = useHistory();
    console.log("mainmeni")
    console.log(userInfo)
    const [state, setState] = React.useState({
        links: [{
            name: "",
            link: `random?tag=newyear`,
            icon: <AcUnitIcon className={classes.rotate} />,
        }, {
            name: "",
            // link: `${properties.repo}/home`,
            link: `home`,
            icon: <HomeIcon />,
        }, {
            name: "",
            // link: `${properties.repo}/random`,
            link: `random`,
            icon: <ContactSupportIcon />,
        }, {
            name: " ",
            link: `donate`,
            icon: <FreeBreakfastIcon />,
        }, {
            name: " ",
            link: `news`,
            icon: <UpdateIcon />,
        }, {
            name: " ",
            link: `about`,
            icon: null
        }],
        selectedIndex: 0,
    })
    const handleListItemClick = (e, index, link) => {
        history.push(`${properties.repo}/${link}`)
    }
    React.useEffect(() => {
        if (!userInfo.userId === null)
            return
        state.links.push({
            name: userInfo.loggedIn == true ? "" : "Sign In / Sign Up",
            link: userInfo.loggedIn ? `logout` : `login`,
            icon: null
        })        
        state.links.push({
            name: "",
            link: `profile`,
            icon: null
        })
        state.links.push({
            name: "",
            link: `createSurvey`,
            icon: null
        })
        setState({ ...state, links: state.links })

    }, [userInfo])
    console.log(window.location)
    return <>
        <Card className={classes.menu}>
            <List disablePadding dense>
                {state.links.map((link, index) => <>
                    {/* <Link to = {link.link} > test </Link>  */}
                    <Link to={`${properties.repo}/${link.link}`}
                        className={classes.inactive}
                        // activeStyle={{
                        //     color: "red",
                        //     background: "linear-gradient(21deg, rgba(54,54,61,0.8132440476190477) 0%, rgba(95,154,164,0.7740283613445378) 100%)"
                        // }}
                        onClick={e => setState({ ...state, selectedIndex: index })} >
                        <ListItem
                            button
                            // selected={state.selectedIndex === index}
                            selected={`${window.location.pathname}${window.location.search}` === `${properties.repo}/${link.link}`}
                            // selected = {true}
                            className={classes.root}
                        >
                            <ListItemIcon
                                className={classes.icon}>
                                {link.icon}
                            </ListItemIcon>
                            <ListItemText primary={link.name} />
                        </ListItem>
                    </Link>
                </>
                )}
            </List>
        </Card>
    </>
}