const useStyles = makeStyles({
    root: {
        width: "75%",
        // paddingLeft: 100,
        // paddingRight: 20,
        marginLeft: 40
    }
});

const renderCustomizedLabel = (props) => {
    
    const {cx, cy, midAngle, innerRadius, outerRadius, percent, value} = props
    const radius = innerRadius + (outerRadius - innerRadius) * 0.5;
    const x = cx + radius * Math.cos(-midAngle * RADIAN);
    const y = cy + radius * Math.sin(-midAngle * RADIAN);
    const option = props.name

    return (<>
    {value === 0 ? '' : 
    <>
      <text x={x} y={y} fontSize = {14} fill="white" textAnchor={x > cx ? 'start' : 'end'} dominantBaseline="central">
        {`${(percent * 100).toFixed(0)}%`}
      </text>
      <text 
        x = {cx + (outerRadius + 30) * Math.cos(-midAngle * RADIAN)} 
        y = {cy + (outerRadius + 30) * Math.sin(-midAngle * RADIAN)}>
        {option}
      </text>
      </>
    }
    </>);
};
const COLORS = ['#0088FE', '#00C49F', '#FFBB28', '#FF8042'];

const RADIAN = Math.PI / 180;

///////////////////////////////////////////////
///////////////////////////////////////////////
///////////////////////////////////////////////

export default function StatisticsComponent(props){
    const classes = useStyles();
    const [value, setValue] = React.useState([0,99]);
    const [genderValue, setGenderValue] = React.useState(2);
    const [fetchedData, setFetchedData] = React.useState([])
    const [data, setData] = React.useState([])
    const [barData, setBarData] = React.useState([])
    const [ratingData, setRatingData] = React.useState([])
    const [filteredDataLength, setFilteredDataLength] = React.useState(0)
    const [legend, setLegend] = React.useState({})
    
    const [barOptions, setBarOptions] = React.useState([]) // For multiple survey

    const renderLegend = (props) => {
      const { payload } = props;
    
      return (
        Object.keys(legend).map(key => 
          <text  style = {{textAlign: "center"}}>
            <text style = {{fontWeight: "bold", color: "steelblue"}}>{key}</text> : 
            {/* {legend[key].map(item => <text style = {{fontStyle: "italic"}}> {item} </text>)} */}
            <text style = {{fontStyle: "italic"}}> {legend[key].join(" + ")} </text>
          </text>
        )
      );
    }

    // React.useEffect(() => {
    //   fetch(`${properties.address}/getStatistics?surveyId=${props.SurveyId}`, 
    //   {
    //     method: 'GET',
    //     redirect: 'follow',
    //     credentials: 'include'
    //   })
    //   .then(response => {
    //       if (response.status === 200){
    //         response.json().then(result => {
    //           setFetchedData(result)
    //         })
    //       }
    //     }
    //   )
    // }, [props.SurveyId])

    React.useEffect(() => {
      if (fetchedData.length === 0)
        return
      // Filtering by gender and age
      let gender = genderValue === 0 ? 'male' : genderValue === 1 ? 'female' : ''
      let filtered_data = fetchedData.filter(elem => 
          (elem.user.age > value[0] && elem.user.age < value[1])
          && ((gender !== '' && elem.user.sex === gender) || (gender === ''))
      )
      setFilteredDataLength(filtered_data.length)

      // Setting data for Pie Chart (Single and Multiple)
      let arr = []
      switch (props.Type){
        case "Multiple":
          arr =  props.Options.map(option => {
            return {
              name: option,
              value : filtered_data.filter(elem => elem.options.includes(option)).length
            }
          }).sort((a,b) => a.value - b.value)
          break
        case "Single":
          arr =  props.Options.map(option => {
            return {
              name: option,
              value : filtered_data.filter(elem => elem.option === option).length
            }
          }).sort((a,b) => a.value - b.value)
          break
        default:
      }
      setData(arr)
      
      // Setting data for Bar Chart (Multiple)
      if (props.Type === "Multiple"){
        let bar_arr = props.Options.map(option => {
          return {
            name: option,
            value: filtered_data.filter(elem => elem.options.includes(option)).length / filtered_data.length * 100
          }
        }).sort((a,b) => b.value - a.value)
        setBarData(bar_arr)
      }

      // Setting data for Bar Chart (Rating)
      if (props.Type === "Rating" && filtered_data.length !== 0){
        let rating_arr = []

        let accepted = []
        let rows = [...props.Rows]
        let index = 0
        let max = 0
        let max_arr = []
        let bar = {}
        let rating = {}

        while (rows.length !== 0){
          bar = {}
          rows.forEach(row => {
            bar[row] = filtered_data.filter(rd => rd.rowscolumns[index] === row).length / (filtered_data.length) * 100
          })
          let sum = Object.values(bar).reduce((a,b) => a + b, 0)
          max = Math.max(...Object.values(bar))

          max_arr = Object.keys(bar).filter(key => bar[key] === max)
          accepted.push(...max_arr)
          rating[props.Columns[index]] = [...max_arr]
          let obj = {}
          obj.name = props.Columns[index]
          rows.forEach(elem => {
            if (bar[elem] !== 0)
              obj[elem] = bar[elem] / sum * 100
          })
          rating_arr.push(obj)

          index++
          rows = props.Rows.filter(row => !accepted.includes(row))
        }
        setLegend(rating)

        setRatingData(rating_arr)
      }

    }, [fetchedData, value, genderValue])

    const handleChange = (event, newValue) => {
      setValue(newValue);
    };
    const handleGenderChange = (event, newValue) => {
      setGenderValue(newValue)
    }

    return (<>
    <Grid container>
    <Grid container>
        <Grid item xs = {7}>
          <Typography color = 'primary'>
            {filteredDataLength} 
          </Typography>
          <div className={classes.root}>
            <Typography id="range-slider" gutterBottom>
              
            </Typography>
            <Slider
              value={value}
              onChange={handleChange}
              valueLabelDisplay="auto"
              aria-labelledby="range-slider"
              max = {99}
            />
            <Slider
              value={genderValue}
              onChange={handleGenderChange}
              valueLabelDisplay="off"
              aria-labelledby="range-slider"
              max = {2}
              marks = {marks}
            />
          </div>
        </Grid>
        <Grid item xs = {5}>
          {(props.Type === "Multiple" || props.Type === "Single") && 
            <PieChart width={200} height={200}>
            <Pie
              data={data}
              cx='50%'
              cy='50%'
              labelLine={true}
              label={renderCustomizedLabel}
              outerRadius={45}
              fill="#8884d8"
              dataKey="value"
            >
              {
                data.map((entry, index) => 
                <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
                )
              }
            </Pie>
          </PieChart>}
        </Grid>
      </Grid>
      <Grid item xs = {12} alignItems = 'center' justify = 'center'>
            
          {props.Type === "Multiple" && 
          <BarChart
            width={400}
            height={200}
            data={barData}
            margin={{
              top: 0, right: 0, left: 0, bottom: 0,
            }}
          >
            <CartesianGrid strokeDasharray="3 3" />
            <XAxis dataKey="name" />
            <YAxis domain = {[0, 100]} />
            <Tooltip />
            <Legend formatter = {() => `%   `}/>
            <Bar dataKey="value" fill="#8884d8" />
          </BarChart>}
          {props.Type === "Rating" && 
          <>
          <BarChart
            width={500}
            height={200}
            data={ratingData}
            margin={{
              top: 0, right: 0, left: 0, bottom: 0,
            }}
          >
            <CartesianGrid strokeDasharray="3 3" />
            <XAxis dataKey="name" />
            <YAxis />
            <Tooltip />
            <Legend 
              content={renderLegend} 
              wrapperStyle = {{textAlign: "center"}}
            />
            {props.Rows.map((row,index) => 
              <Bar dataKey = {row} stackId="a" fill={COLORS[index % COLORS.length]} />
            )}
          </BarChart>
          </>}
      </Grid>
    </Grid>
    </>)
}