import React from 'react';
import PropTypes from 'prop-types';
import { fade, makeStyles } from '@material-ui/core/styles';
import Toolbar from '@material-ui/core/Toolbar';
import Button from '@material-ui/core/Button';
import SearchIcon from '@material-ui/icons/Search';
import Typography from '@material-ui/core/Typography';
import AppBar from '@material-ui/core/AppBar';
import InputBase from '@material-ui/core/InputBase';
import { Link } from 'react-router-dom'
import AccountIconComponent from './AccountIconComponent'
import { properties } from '../Properties/Properties.js';

const useStyles = makeStyles((theme) => ({
  logo: {
    maxWidth: "40px",
  },
  toolbar: {
    borderBottom: `1px solid ${theme.palette.divider}`,
    backgroundColor: theme.palette.info
  },
  toolbarTitle: {
    flex: 1,
    marginLeft: 20
  },
  toolbarSecondary: {
    justifyContent: 'space-between',
    overflowX: 'auto',
  },
  toolbarLink: {
    padding: theme.spacing(1),
    flexShrink: 0,
    color: 'black'
  },
  search: {
    position: 'relative',
    borderRadius: theme.shape.borderRadius,
    backgroundColor: fade(theme.palette.common.white, 0.15),
    '&:hover': {
      backgroundColor: fade(theme.palette.common.white, 0.25),
    },
    marginLeft: 10,
    //width: '100%',
    [theme.breakpoints.up('sm')]: {
      marginLeft: theme.spacing(1),
      width: 'auto',
    },
  },
  searchIcon: {
    padding: theme.spacing(0, 2),
    height: '100%',
    position: 'absolute',
    pointerEvents: 'none',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
  },
  inputRoot: {
    color: 'inherit',
  },
  inputInput: {
    padding: theme.spacing(1, 1, 1, 0),
    // vertical padding + font size from searchIcon
    paddingLeft: `calc(1em + ${theme.spacing(4)}px)`,
    transition: theme.transitions.create('width'),
    width: '100%',
    // [theme.breakpoints.up('sm')]: {
    //   width: '12ch',
    //   '&:focus': {
    //     width: '100ch',
    //   },
    // },
  },
}));

export default function Header(props) {
  const classes = useStyles(props);
  const { title } = props;

  return (
    <React.Fragment>
      {/* <a href = "/about.html">About</a> */}

      <AppBar position="static" className={classes.toolbar}>
        <Toolbar >
          <Link to={`${properties.repo}/home`}>
            <img src="images/logo.png" alt="" className={classes.logo}></img>
          </Link>
          <Typography
            component="h2"
            variant="h6"
            color="inherit"
            align="left"
            noWrap
            className={classes.toolbarTitle}
          >
            <Link to={`${properties.repo}/home`} style={{ textDecoration: 'none', color: 'inherit' }}>
              
            </Link>
            <span> | </span>
            <Link to={`${properties.repo}/news`} style={{ textDecoration: 'none', color: 'inherit' }}>
              
            </Link>
            <span> | </span>
            <Link to={`${properties.repo}/donate`} style={{ textDecoration: 'none', color: 'inherit' }}>
               
            </Link>
            <span> | </span>
            <Link to={`${properties.repo}/about`} style={{ textDecoration: 'none', color: 'inherit' }}>
               
            </Link>
          </Typography>

          {/* <div className={classes.search}>
            <div className={classes.searchIcon}>
              <SearchIcon />
            </div>
            <InputBase
              placeholder="Search"
              classes={{
                root: classes.inputRoot,
                input: classes.inputInput,
              }}
              inputProps={{ 'aria-label': 'search' }}
            />
          </div> */}

          {props.state.loggedIn ? <>
            <AccountIconComponent {...props}></AccountIconComponent>
          </>
            :
            <>
              <Link to={`${properties.repo}/login`} style={{ textDecoration: "none" }}>
                <Button variant="text" size="small">
                  Login
            </Button>
              </Link>
              <Link to={`${properties.repo}/register`} style={{ textDecoration: "none" }}>
                <Button variant="outlined" size="small">
                  <Typography noWrap>Sign up</Typography>
                </Button>
              </Link>
            </>}

        </Toolbar>
      </AppBar>
    </React.Fragment>
  )
}
Header.propTypes = {
  sections: PropTypes.array,
  title: PropTypes.string,
};