import React from 'react';

import Grid from '@material-ui/core/Grid';
import { properties } from '../Properties/Properties.js';
import MultipleSurveyComponent from "./MultipleSurveyComponent";
import SingleSurveyComponent from "./SingleSurveyComponent";
import RatingSurveyComponent from "./RatingSurveyComponent";
import CardComponent from "./CardComponent";
import { useHistory } from "react-router-dom";
import { PopupContext } from '../index'


export default function BaseSurveyComponent(props) {

  const history = useHistory()
  // const classes = useStyles()
  const [state, setState] = React.useState({
    helperText: " ,    ",
    answered: props.surveyJson.answered
  })
  const img = props.surveyJson.image;
  const setMessageFunc = React.useContext(PopupContext)

  const getStats = (e, processDataFunc) => {
    fetch(`${properties.address}/getStats?surveyId=${props.surveyJson.id}`,
      {
        method: 'GET',
        redirect: 'follow',
        credentials: 'include'
      })
      .then(response => {
        if (response.status === 200) {
          response.json().then(result => {
            processDataFunc(e, result)
            // setFetchedData(result)
          })
        }
      }
      )
  }

  const handleSubmit = (e, submitOptions, validatingFunc, processDataFunc) => {
    e.preventDefault()

    if (!validatingFunc())
      return

    fetch(`${properties.address}/answerSurvey`,
      {
        method: 'POST',
        headers: {
          "Content-Type": "application/json"
        },
        body: JSON.stringify(submitOptions),
        redirect: 'follow',
        credentials: 'include'
      })
      .then(response => {
        if (response.status === 200) {
          // props.votedCallback()
          console.log('base: answered')
          setState({ ...state, answered: true })
        }
        else
          props.History.push('/login')
      }
      )
  }

  const returnBody = e => {
    let surveyBody
    let params = { "getStats": getStats, "Answered": state.answered, "handleSubmit": handleSubmit, "surveyJson": props.surveyJson, "HelperText": state.helperText, "History": history }
    switch (props.surveyJson.type) {
      case "Single":
        surveyBody = <SingleSurveyComponent props={params} />
        break
      case "Multiple":
        surveyBody = <MultipleSurveyComponent props={params} />
        break
      case "Rating":
        surveyBody = <RatingSurveyComponent props={params} />
        // body = "404"
        break
      default:
        surveyBody = <div>,    -  </div>
    }
    console.log('setting answered to ' + state.answered)
    let baseSurvey = <CardComponent
      Answered={state.answered}
      surveyJson={props.surveyJson}
      LoggedInUserId={props.UserId}
      LoggedInUsername={props.UserName}
    >
      <Grid container>
        <Grid item xs={img === '' || img === null ? 0 : 4}>
          <img alt="" style={{ width: "100%", maxWidth: "200px" }} src={img}></img>
        </Grid>
        <Grid item xs={img === "" || img === null ? 12 : 8}>
          {surveyBody}
        </Grid>
      </Grid>
      {/* <Dialog
        open={this.state.open}
        onClose={this.handleClose}
      >
        <DialogContent>
          <DialogContentText>
            <img alt="" style={{ width: "100%" }} src={this.state.img}></img>
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={this.handleClose} color="primary">
            
          </Button>
        </DialogActions>
      </Dialog> */}
    </CardComponent >
    return baseSurvey
  }
  return <>
    {returnBody()}
  </>
}